import {
    Modal
} from "antd"

const success = (text) =>{
    Modal.success({
        title: "",
        content: text
    })
}

const error = (text) =>{
    Modal.error({
        title: "",
        content: text
    })
}

const warning = (text) =>{
    Modal.warning({
        title: "",
        content: text
    })
}
export {success, error, warning} 