import React from "react";
import { Breadcrumb } from 'antd'
class Operation extends React.Component {
    constructor(props) {
        super(props)
    }
    render() {
        return (
            <div>
                <Breadcrumb>
                    <Breadcrumb.Item></Breadcrumb.Item>
                    <Breadcrumb.Item></Breadcrumb.Item>
                </Breadcrumb>
                <span></span>
            </div>
        )
    }
}

export default Operation